.main-container {
  max-width: 1140px;
  padding: 0 20px;
  margin: 0 auto;
}
.voice-btn {
  margin-bottom: 10px;
}
#investmentAmount {
  max-width: 375px;
}
#investTab .nav-link {
  color: #fff;
}
#investTab .nav-link:hover,
#investTab .nav-link.active {
  color: #fff;
}
@media (max-width: 1150px) {
  .main-container .tab-back {
    width: 60%;
  }
}
@media (max-width: 992px) {
  .main-container .tab-back {
    width: 100%;
  }
}
@media (max-width: 576px) {
  .investment-table .service-buttons {
    width: 132px;
  }
  .investment-table .btn {
    min-width: 94px;
  }
  .more-invest {
    overflow-x: scroll;
  }
}
@media (max-width: 388px) {
  .btn-primary_mt {
    margin-top: -9px;
  }
}
@media (max-width: 362px) {
  #investmentAmount {
    max-width: 159px;
  }
}
.small-modal {
  max-width: 362px;
}
.invest-card-heading {
  opacity: 0;
}
.invest-grid {
  display: grid;
  grid-template-areas: "card-1 card-2 video" "baner baner video";
  column-gap: 24px;
  row-gap: 10px;
  grid-template-columns: 270px 270px minmax(374px, 516px);
  grid-template-rows: auto 105px;
}
.card-1 {
  grid-area: card-1;
}
.card-2 {
  grid-area: card-2;
}
.card-2 .btn-warning {
  font-size: 10px;
  line-height: 13px;
}
.invest-card {
  background: #080031;
  border-radius: 10px;
  padding: 17px 14px 13px;
}
.invest-card__header {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  color: #E1DFEC;
}
.invest-card__body {
  margin-top: -24px;
}
.invest-card__body label {
  font-size: 0;
  line-height: 0;
}
.invest-card__body .form-control {
  font-size: 32px;
  line-height: 48px;
  letter-spacing: 0.04em;
  color: rgba(225, 223, 236, 0.5);
}
.invest-card__footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.invest-card__footer .btn-warning {
  padding: 0;
}
.invest-baner {
  grid-area: baner;
  padding: 15px 20px;
  background: #725BFF;
  border-radius: 10px;
  position: relative;
  background-image: url('../images/themes/money.png');
  background-repeat: no-repeat;
  background-position: 130% 25%;
  background-size: 70%;
}
.invest-baner ::before {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: #715bff36;
}
.invest-baner__text {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 118%;
  color: #FFFFFF;
  max-width: 200px;
  margin-bottom: 8px;
  position: relative;
  z-index: 1;
}
.invest-baner__linck {
  display: inline-block;
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 700;
  font-size: 10px;
  line-height: 15px;
  text-decoration: none;
  color: #FFFFFF;
  padding: 6px 8px;
  border: 1px solid #FFFFFF;
  border-radius: 8px;
  transition: all 0.4s ease-in;
}
.invest-baner__linck:hover,
.invest-baner__linck:focus {
  text-decoration: none;
  color: #2F0DFF;
  background: #fff;
}
.video-card {
  grid-area: video;
  border-radius: 10px;
  background: #080031;
  padding: 18px 16px 20px;
  margin-top: 38px;
}
.video-group-btn {
  margin-top: 15px;
  display: flex;
  align-items: center;
}
.lang-btn {
  font-weight: 700;
  font-size: 12px;
  line-height: 18px;
  color: #FFFFFF;
  padding: 6px 13px;
  margin: 0 2px;
  border: 1px solid #FFFFFF;
  border-radius: 8px;
  background: transparent;
  transition: all 0.4s ease-in;
}
.lang-btn:hover,
.lang-btn:focus {
  color: #2F0DFF;
  background: #fff;
  border: 1px solid #FFFFFF;
}
.print-btn {
  font-weight: 700;
  font-size: 12px;
  line-height: 18px;
  color: #FFFFFF;
  padding: 6px 13px;
  background: #2F0DFF;
  border-radius: 8px;
  border: none;
  transition: all 0.4s ease-in;
}
.print-btn:focus,
.print-btn:hover {
  color: #2F0DFF;
  background: #fff;
}
.btn-label {
  font-weight: 400;
  font-size: 10px;
  line-height: 15px;
  color: rgba(255, 255, 255, 0.5);
  margin-right: 14px;
}
.lang-btn-group {
  margin-left: auto;
}
.video {
  border-radius: 10px;
}
.invest-video {
  position: relative;
}
.video-play-btn {
  position: absolute;
  display: inline-block;
  width: 52px;
  height: 52px;
  top: 50%;
  left: 50%;
  border: none;
  background-color: transparent;
  background-image: url('../images/icons/play_btn.png');
  background-position: center center;
  background-repeat: no-repeat;
  overflow: hidden;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;
}
@media (max-width: 1208px) {
  .invest-grid {
    grid-template-areas: "card-1 card-2 " "baner baner" "video video";
    row-gap: 20px;
  }
  .video-card {
    margin-top: 0;
  }
}
@media (max-width: 660px) {
  .invest-grid {
    grid-template-areas: "card-1 baner" "card-2 baner" "video video";
    grid-template-columns: auto auto;
    grid-template-rows: auto auto auto;
  }
  .invest-baner {
    background-position: 90% 100%;
    background-size: 140%;
  }
  .invest-baner__text {
    margin-bottom: 20px;
  }
  .card-1,
  .card-2 {
    max-width: 270px;
  }
}
@media (max-width: 440px) {
  .invest-grid {
    grid-template-areas: "card-1 card-1" "card-2 card-2" "baner baner" "video video";
    grid-template-columns: auto auto;
    grid-template-rows: auto auto 105px auto;
  }
  .invest-baner {
    background-position: 130% 25%;
    background-size: 70%;
  }
  .invest-baner__text {
    margin-bottom: 8px;
  }
  .video-play-btn {
    width: 42px;
    height: 42px;
  }
}
@media (max-width: 380px) {
  .btn-label {
    display: none;
  }
}
